import { Input, Form, DatePicker, InputNumber, Row, Col, Select, Switch } from "antd";
import moment from "moment";

export class MyInput {
    // ************************* Input ****************************
    static Input(props: any) {
        function handleChangeInput(value: any) {
            if (props.onChange !== undefined) {
                props.onChange(value.target.value);
            }

            if (props.setValue !== undefined) {
                props.setValue(value.target.value);
            }            
        }

        return (
            <Input
                onClick={props.onClick}
                value={props.value}
                onChange={handleChangeInput}
                required={props.required}
                readOnly={props.readOnly} />
        );
    }
}

export class MyFormItem {
    static Input(props: any) {
        return (
            <Form.Item label={props.label}>
                <MyInput.Input
                    onClick={props.onClick}
                    onChange={props.onChange}
                    value={props.value}
                    setValue={props.setValue}
                    required={props.required}
                    readOnly={props.readOnly} />
            </Form.Item>
        );
    }

    // *************************** Datepicker **************************
    static DatePicker(props: any) {
        function handleChangeOption(value: any) {
            if (props.setValue !== undefined) {
                props.setValue(value);
            }
        }

        function customeFormat(value: any) {
            return moment(value).format("DD/MM/YYYY");
        }

        return (
            <Form.Item label={props.label}>
                <DatePicker
                    value={props.value}
                    onChange={handleChangeOption}
                    format={customeFormat}
                    inputReadOnly={props.readOnly} />
            </Form.Item>
        );
    }

    // *************************** Datetimepicker **************************
    static DateTimePicker(props: any) {
        function handleChangeOption(value: any) {
            if (props.setValue !== undefined) {
                props.setValue(value);
            }
        }

        function customeFormat(value: any) {
            return moment(value).format("DD/MM/YYYY HH:mm:ss");
        }

        return (
            <Form.Item label={props.label}>
                <DatePicker
                    showTime
                    value={props.value}
                    onChange={handleChangeOption}
                    format={customeFormat}
                    disabled={props.readOnly} />
            </Form.Item>
        );
    }

    // *************************** Number **************************
    static Number(props: any) {
        function handleChangeInput(value: any) {
            if (props.setValue !== undefined) {
                props.setValue(value);
            }
        }

        return (
            <InputNumber
                min={props.min}
                max={props.max}
                value={props.value}
                onChange={handleChangeInput} />
        );
    }

    static NumberColSpan(props: any) {        
        return (
            <div>
                <Row className={props.className}>
                    <Col span={props.colSpan1}>
                        <span>{props.label}</span>
                    </Col>
                    <Col span={props.colSpan2}>
                        <MyFormItem.Number
                            min={props.min}
                            max={props.max}
                            value={props.value}
                            setValue={props.setValue} />
                    </Col>
                </Row>
            </div>
        );
    }

    // *************************** Select **************************
    static Select(props: any) {
        function handleChangeOption(value: any) {
            if (props.setValue !== undefined) {
                props.setValue(value);
            }

            if (props.onChange !== undefined) {
                props.onChange(value);
            }
        }

        return (
            <Form.Item label={props.label}>
                <Select
                    options={props.options}
                    value={props.value}
                    onChange={handleChangeOption} />
            </Form.Item>
        );
    }


    // *************************** TextArea **************************
    static TextArea(props: any) {
        function handleChangeOption(value: any) {
            if (props.setValue !== undefined) {
                props.setValue(value.target.value);
            }
        }

        const { TextArea } = Input;
        return (
            <Form.Item label={props.label}>
                <TextArea
                    value={props.value}
                    onChange={handleChangeOption} />
            </Form.Item>
        );
    }

    // *************************** Password **************************
    static Password(props: any) {
        function handleChangeOption(value: any) {
            if (props.onChange !== undefined) {
                props.onChange(value.target.value);
            }

            if (props.setValue !== undefined) {
                props.setValue(value.target.value);
            }
        }

        const { Password } = Input;
        return (
            <Form.Item label={props.label}>
                <Password
                    value={props.value}
                    onChange={handleChangeOption} />
            </Form.Item>
        );
    }

    // *************************** Search **************************
    static Search(props: any) {
        function handleChangeSearch(value: any) {
            if (props.setValue !== undefined) {
                props.setValue(value.target.value);
            }
        }

        const { Search } = Input;
        return (
            <Form.Item label={props.label}>
                <Search
                    loading={props.loading}
                    onSearch={props.onSearch}
                    value={props.value}
                    onChange={handleChangeSearch} />
            </Form.Item>
        );
    }

    // *************************** Swith **************************
    static Switch(props: any) {
        function handleChangeSearch(value: any) {
            if (props.setValue !== undefined) {
                props.setValue(value);
            }
            
            if(props.onChange !== undefined) {
                props.onChange(value);
            }
        }

        return (
            <Form.Item label={props.label}>
                <Switch
                    loading={props.loading}
                    checked={props.value}
                    onChange={handleChangeSearch} />
            </Form.Item>
        );
    }

     // *************************** Swith **************************
     static File(props: any) {
        function handleChange(value: any) {
            if (props.setValue !== undefined) {
                props.setValue(value.target.files[0]);
            }
        }

        return (
            <Form.Item label={props.label}>
                <Input
                    type="file"
                    // value={props.value}
                    onChange={handleChange}/>
            </Form.Item>
        );
    }
}