import { Form } from "antd";
import { useState } from "react";
import { Link } from "react-router-dom";
import { Fn } from "../../helper/function";
import { AuthService } from "../../services/authService";
import { MyFormItem } from "../core/Input";
import { MyBox } from "../core/MyBox";
import { MyButton } from "../core/MyButton";

export default function ForgotPasswordComponent() {
    const [isLoading, setIsLoading] = useState(false);
    const [userName, setUserName] = useState('');
    const [otp, setOtp] = useState('');

    function handleClickBtnSendOTP() {
        setIsLoading(true);
        let body = {
            email: userName
        };

        AuthService.RequestForgotPassword(body)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    Fn.PrintMsgSuccessV2(rs);
                }
            })
            .finally(() => setIsLoading(false))
    }

    function handleClickBtnResetPassword() {
        setIsLoading(true);
        let body = {
            email: userName,
            otp: otp
        };

        AuthService.VerifyForgotPassword(body)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    Fn.PrintMsgSuccessV2(rs);
                }
            })
            .finally(() => setIsLoading(false))
    }

    return (
        <MyBox title="Quên mật khẩu" >
            <Form layout="vertical">
                <MyFormItem.Input
                    label="Tên đăng nhập"
                    value={userName}
                    setValue={setUserName} />
                <MyButton
                    value="Gửi mã OTP"
                    isLoading={isLoading}
                    onClick={handleClickBtnSendOTP} />
                <MyFormItem.Password
                    label="OTP"
                    value={otp}
                    setValue={setOtp} />

                <p>Nhấn vào <Link to="login">đây</Link> để trở về trang đăng nhập</p>
                <MyButton
                    value="Đổi mật khẩu"
                    isLoading={isLoading}
                    onClick={handleClickBtnResetPassword} />
            </Form>
        </MyBox>
    )
}